import { useEffect, useRef } from 'react';

const WordCard = (props) => {
    const {
        currentWordSet,
        guessedPartOfWord,
        inputConfig,
        checkAnswer,
    } = props;
    const inputFocusedRef = useRef(null);
    const increment = guessedPartOfWord.length;

    useEffect(() => {
      inputFocusedRef.current?.focus();
    }, [increment]);

    return (
        <div className="card-content">
          <h5 className="word-explanation">{currentWordSet.translation}</h5>
          <form>
            <div className="puzzle-set">
              <ol className="blank-letters">
                {currentWordSet.word.split('').map((elem, index) => (
                  <input
                    {...inputConfig}
                    key={index}
                    ref={
                      index === increment
                        ? inputFocusedRef
                        : null
                    }
                    disabled={index !== increment}
                    value={guessedPartOfWord[index] || ''}
                    data-value={elem}
                    onChange={checkAnswer}
                  />
                ))}
              </ol>
              <ul className="letters-set">
                {currentWordSet.shakenArray.map(
                  (elem, index) => (
                    <li
                      className="letter"
                      data-value={elem}
                      key={index}
                      onClick={checkAnswer}
                    >
                      {elem}
                    </li>
                  )
                )}
              </ul>
            </div>
          </form>
        </div>
    );
};

export default WordCard;