import React, { useState } from "react";

import "./ChristmasAdditional.css";
import "./Christmas.css";


export const Lightrope = () => {
  let bulbs = [];
  for (let i = 0; i < 43; i++) {
    bulbs.push(i);
  }

  return (
    <ul className="lightrope">
      {bulbs.map(elem => (
        <li key={elem} />
      ))}
    </ul>
  );
};

export const Present = props => {
  let [presentIsOpen, openPresent] = useState({
    isOpen: false,
    isCardShown: false
  });
  let sideIcons = [
    "candy-cane",
    "sleigh",
    "hat-wizard",
    "tree",
    "star",
    "gifts"
  ];
return null;
  const presentMoveHandler = () => {
    let present = document.querySelector(".cube");
    present.onmousemove = e => {
      present.style.transform =
        "rotateX(" + -e.pageY + "deg) rotateY(" + -e.pageX + "deg)";
    };
    present.ondblclick = () => {
      present.onmousemove = null;
      present.style.transform = "";
      let opacity = 1;
      let timer = setInterval(() => {
        if (opacity <= 0) {
          clearInterval(timer);
          openPresent({ isOpen: true, isCardShown: true });
        } else {
          opacity -= 0.05;
          let sides = document.querySelectorAll(".cube>div");
          for (let i = 0; i < sides.length; i++) {
            sides[i].style.opacity = opacity;
          }
        }
      }, 70);
    };
  };

  return !presentIsOpen.isOpen ? (
    <div className="block present" onClick={presentMoveHandler}>
      <div className="cube">
        {Array.from({ length: 6 }).map((elem, index) => (
          <div className={"side-" + (index + 1)} key={index}>
            <i className={"fas fa-" + sideIcons[index]} />
          </div>
        ))}
      </div>
    </div>
  ) : presentIsOpen.isCardShown && presentIsOpen.isOpen ? (
    <div
      className="wishCard"
      onDoubleClick={() => openPresent({ isOpen: true, isCardShown: false })}
    >
      <div className="card">
        <div className="front face">
          <span>To: For you</span>
          <span>From: Santa, The North Pole</span>
        </div>
        <div className="back face">
          <h2>
            Merry Christmas!{" "}
            <span>Let sometheng miraculous happen this year!</span>
          </h2>
        </div>
      </div>
    </div>
  ) : null;
};
