import React, { useState } from "react";
import Logo from "../../Logo/Logo";
import NavigationItems from "../NavigationItems/NavigationItems";
import SideDrawer from "../SideDrawer/SideDrawer";
import DrawerToggleButton from "../SideDrawer/DrawerToggleButton/DrawerToggleButton";
import Overlay from '../../UI/Overlay/Overlay';
import "./Toolbar.css";

let currentYScrollPosition = 0;

const onSideDrawerOpen = () => {
  currentYScrollPosition = window.scrollY;

  document.body.style.position = 'fixed';
  document.body.style.height = 'auto';
  document.body.style.top = `-${currentYScrollPosition}px`;
  document.body.classList.add('ov-hidden');
};

  const onSideDrawerClose = () => {
    const scrollY = Math.abs(parseInt(document.body.style.top));

    document.body.classList.remove('ov-hidden');
    document.body.style.position = '';
    document.body.style.top = '';
    document.body.style.height = '';
    window.scrollTo(0, scrollY);
  };

const Toolbar = (props) => {
  let state = { sideDrawerIsOpen: false };
  let [isOpen, toggle] = useState(state);

  const toggleSideDrawer = (e) => {
    if(e.target.nextElementSibling && e.target.nextElementSibling.classList.contains('submenu')) return;
    isOpen.sideDrawerIsOpen ? onSideDrawerClose() : onSideDrawerOpen();
    toggle(prevState => ({ sideDrawerIsOpen: !prevState.sideDrawerIsOpen }));
  };
  
  return (
    <div className="Toolbar">
      <SideDrawer
        isAuth={props.isAuth}
        isOpen={isOpen.sideDrawerIsOpen}
        clickHandler={toggleSideDrawer}
      />
      <DrawerToggleButton click={toggleSideDrawer} />
      <Logo />
      <nav>
        <NavigationItems isAuth={props.isAuth} />
      </nav>
      <Overlay
        show={isOpen.sideDrawerIsOpen}
        additionalClassNames="frozen -dark"
      />
    </div>
  );
};

export default Toolbar;
