import { useEffect } from 'react';
import { useLocation, useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import { md } from '../../utilities/utilities';
import * as actionCreators from '../../store/actions/index';
import { getCommonActions } from '../../store/reducers/commonReducer';
import Spinner from '../UI/Spinner/Spinner';
import {
    US_LOCALE,
    pathSheetIdPairs,
    getTaskPathByPageId,
    getSpreadSheetUrlWithRanges,
} from '../../../constants';
import '../../containers/Blog/Blog.scss';
import '../Card/Card.scss';

const actions = getCommonActions('BLOG');

const Article = (props) => {
    const {
        data,
        loading,
        error,
        fetchDataConnect,
    } = props;
    const {
        state,
    } = useLocation();
    const { token } = useParams();

    useEffect(() => {
        const {
            fetchStart: fetchStartHandler,
            fetchSuccess: fetchSuccessHandler,
            fetchFail: fetchFailHandler,
        } = actions;

        if (!data || !data.length) {
            const pageId = pathSheetIdPairs['/news'];
            const taskPath = getTaskPathByPageId(pageId);
            const requestUrl = getSpreadSheetUrlWithRanges(taskPath, '2:15');
            
            fetchDataConnect(
                requestUrl,
                {
                    fetchStartHandler,
                    fetchSuccessHandler,
                    fetchFailHandler,
                },
            );
        }
    
        // return () => restoreInitialStateConnect();
    }, []);

    if (!data) {
        return <Spinner />
    }
    
    const articleContent = data.find((el) => el.blogURL.slice(1) === token);
    const {
        blogURL,
        imageURL,
        imageAlt,
        title,
        updateDate,
        creationDate,
        content,
        shortDescription,
        author,
    } = articleContent;
    const markdown = md.render(content);

    return (
        <article className="Article wrap">
            <h1>{title}</h1>
            <p className="short-description">{shortDescription}</p>
            <figure className='image-container -bordered'>
                <img src={imageURL} alt={imageAlt} />
            </figure>
            <p className="publication-details">
                <span className='creation-date'>The Article Was Posted: {new Date(+creationDate).toLocaleDateString(US_LOCALE)}</span>
                <span className="mobile-hide"> / </span>
                <span>By {author}</span>
            </p>
            {updateDate
            ? ( <p className='update-date'>The Article Was Updated: {new Date(+updateDate).toLocaleDateString(US_LOCALE)}</p>)
            : null}
            <hr />
            <div dangerouslySetInnerHTML={{ __html: markdown }} />
        </article>
    );
};

const mapStateToProps = (state) => ({
    data: state.blog.fetchedData,
    loading: state.blog.loading,
    error: state.blog.error,
  });
  
  const mapDispatchToProps = (dispatch) => ({
    fetchDataConnect: (url, options) => dispatch(actionCreators.asyncFetchStart(url, options)),
    restoreInitialStateConnect: () => dispatch(actions.restoreInitialState()),
  });

export default connect(mapStateToProps, mapDispatchToProps)(Article);