// const commonReducer = ReducerFactory(initialState, actions);

const getCommonActions = (moduleName) => {
  const actions = ActionCreatorsFactory(
    { moduleName },
    {
    ...commonActions,
    // Component-specific reducers can go here
    }
  );

  return actions;
};

const getCommonReducer = (actions) => {
  const initialState = getInitialStateInstance();
  const reducer = ReducerFactory(initialState, actions);

  return reducer;
}; 

// export default commonReducer;
export {
  getCommonReducer,
  getCommonActions,
};