import React, { Suspense, lazy } from "react";
import { Routes, Route, HashRouter, Link } from 'react-router-dom';
import { injectAsyncReducer } from './js/react_modules/store/reducers';
import { getCommonActions, getCommonReducer } from "./js/react_modules/store/reducers/commonReducer";
import store from './js/react_modules/store/store';
import spellWordReducer from './js/react_modules/store/reducers/spellWordReducer';

import Layout from "./js/react_modules/HOC/Layout/Layout";
import ScrollToTop from './js/react_modules/HOC/ScrollToTop';
import List from './js/react_modules/components/List/List';
import WordsConstructor from "./js/react_modules/containers/WordsConstructor/WordsConstructor";
import Article from "./js/react_modules/components/Texts/Article";
import Alphabet from "./js/react_modules/components/Alphabet/Alphabet";

import Auth from "./js/react_modules/containers/Auth/Auth";
import Logout from "./js/react_modules/containers/Auth/Logout/Logout";

import Text from './js/react_modules/components/Texts/Text';
import Video from './js/react_modules/components/Video/Video';
import Button from './js/react_modules/components/UI/Button/Button';
import Spinner from './js/react_modules/components/UI/Spinner/Spinner';
import { videoData } from './js/constants';
import './styles/App.scss';
import SelectCorrectOption from "./js/react_modules/containers/SelectCorrectOption/SelectCorrectOption";

const importModuleAsync = (moduleImport, onSuccessCallback) => {
  return new Promise((resolve) => {
    moduleImport
      .then((module) => {
        onSuccessCallback && onSuccessCallback();

        return resolve(module);
      })
      .catch((err) => {
        console.error(err);
      });
  });
};

const SynonymsAsync = lazy(()=> import('./js/react_modules/containers/OppositeWords/OppositeWords'));
const DragAndDropAsync = lazy(() => import('./js/react_modules/containers/DragAndDrop/DragAndDrop'));
const SentencesConstructorAsync = lazy(() => import('./js/react_modules/containers/SentencesConstructor/SentencesConstructor'));
const WordDefenitionMatchAsync = lazy(() => import('./js/react_modules/containers/WordDefenitionMatch/WordDefenitionMatch'));
const BlogAsync = lazy(() => import('./js/react_modules/containers/Blog/Blog'));
const SpellWordAsync = lazy(() => importModuleAsync(
  import('./js/react_modules/containers/SpellWord/SpellWord'),
  () => injectAsyncReducer(store, 'spellWord', spellWordReducer),
));
const ColorIdiomsAsync = lazy(() => importModuleAsync(
  import('./js/react_modules/containers/Idioms/ColorIdioms'),
  () => injectAsyncReducer(store, 'colorIdioms', getCommonReducer(getCommonActions('colorIdioms')),),
));
const FindMatchAsync = lazy(() => importModuleAsync(
  import('./js/react_modules/containers/FindMatch/FindMatch'),
  () => injectAsyncReducer(store, 'findMatch', getCommonReducer(getCommonActions('findMatch')),),
));
const OppositeWordsAsync = lazy(() => importModuleAsync(
  import('./js/react_modules/containers/OppositeWords/OppositeWords'),
  () => injectAsyncReducer(store, 'oppositeWords', getCommonReducer(getCommonActions('oppositeWords')),),
));
const FillGapsAsync = lazy(() => importModuleAsync(
  import('./js/react_modules/containers/FillGaps/FillGaps'),
  () => injectAsyncReducer(store, 'fillGaps', getCommonReducer(getCommonActions('fillGaps')),),
));

const returnVideoChildren = (id) => {
  switch (id) {
    case 7:
      return (
        <div className="video-additional-content">
          <h4>Read the article and do the exercise below</h4>
          <a href="https://www.investopedia.com/ask/answers/032615/how-can-marginal-utility-explain-diamondwater-paradox.asp" className="mb-half">Why Are Diamonds Worth More Than Water?</a>
          <div className="btn-wrap">
            <Link to="/find_match/3:7">
              <Button classNames="btn-3d -blue-pale -short">To task</Button>
            </Link>
          </div>
        </div>
      );
    case 10:
      return (
        <div className="video-additional-content">
          <h4>Practice your pronunciation in the exercise below</h4>
          <div className="btn-wrap">
            <Link to="/spell-word/12:17">
              <Button classNames="btn-3d -blue-pale -short">To task</Button>
            </Link>
          </div>
        </div>
      );
      case 11:
        return (
          <div className="video-additional-content">
            <h4>Practice your words knowledge in the exercise below</h4>
            <div className="btn-wrap">
              <Link to="/words-constructor/22:30">
                <Button classNames="btn-3d -blue-pale -short">To task</Button>
              </Link>
            </div>
          </div>
        );
  }
};

const renderVideoRoutes = () => {
 return videoData.map((el) => (
    <Route path={el.path} element={<Video
      key={el.id}
      url={el.url}
      taskLink={el.taskLink}
      desc={el.title}
      children={returnVideoChildren(el.id)}
    />} />
  ));
};

const APP = () => {
  const routes = (
    <Routes>
      <Route path="/auth" element={<Auth />} />
      <Route path="/logout" element={<Logout />} />
      <Route path="/alphabet" element={<Alphabet />} />
      <Route path="/texts" element={<List content={[{desc: 'Rites of Passage', url: '/texts/2:2'}]} />} />
      <Route path="/texts/:token" element={<Text />} />
      <Route path="/select-correct-option/:token" element={<SelectCorrectOption />} />
      <Route path="/synonyms" element={<List content={[
        {desc: 'Unit 1', url: '/connect-pairs/3:15'},
        {desc: 'Unit 2', url: '/connect-pairs/17:26'},
        {desc: 'Unit 3', url: '/connect-pairs/28:34'},
        {desc: 'Unit 4', url: '/connect-pairs/36:43'},
      ]} />} />
      <Route path="/word-defenition/:token" element={<WordDefenitionMatchAsync />} />
      <Route path='/videos' element={<List content={videoData.map((el) => ({
        desc: el.title,
        url: el.path,
        subtitle: el.subtitle,
      }))} />} />
      {renderVideoRoutes()}
      <Route path='/drag-and-drop/:token' element={<DragAndDropAsync />} />
      <Route path='/find_match/:token' element={<FindMatchAsync />} />
      <Route path='/color-idioms' element={<ColorIdiomsAsync />} />
      <Route path="/logout" element={<Logout />} />
      <Route path='/fill-gaps/:token' element={<FillGapsAsync />} />
      <Route path="/connect-pairs/:token" element={<OppositeWordsAsync />} />
      <Route path="/words-constructor/:token" element={<WordsConstructor />}  />
      <Route path="/sentences-constructor/:token" element={<SentencesConstructorAsync />} />
      <Route path="/news" element={<BlogAsync />} />
      <Route path="/article/:token" element={<Article />} />
      <Route path='/spell-word/:token' element={<SpellWordAsync />} />
      <Route path="/" element={<SentencesConstructorAsync />} />
      {/* <Navigate to="/" make default route here /> */}
    </Routes>
  );

  return (
    <HashRouter>
      <ScrollToTop>
        <Layout>
          <Suspense fallback={<div style={{ height: '80vh' }}><Spinner /></div>}>
            {routes}
          </Suspense>
        </Layout>
      </ScrollToTop>
    </HashRouter>
  );
};

export default APP;
