import axios from "axios";
import * as actionTypes from "./actionTypes";

const authStart = () => {
  return {
    type: actionTypes.AUTH_START
  };
};

const authSuccess = (token, userId) => {
  return {
    type: actionTypes.AUTH_SUCCESS,
    idToken: token,
    userId: userId
  };
};

const authFail = err => {
  return {
    type: actionTypes.AUTH_FAIL,
    error: err
  };
};

export const logOut = () => {
  // alert("log out");
  localStorage.removeItem("token");
  localStorage.removeItem("expirationDate");
  localStorage.removeItem("userId");
  return {
    type: actionTypes.AUTH_LOG_OUT
  };
};

const asyncCheckAuthTimeout = expirationTime => {
  return dispatch => {
    setTimeout(() => {
      dispatch(logOut());
    }, expirationTime * 1000);
  };
};

export const asyncAuth = (email, pass, isSignUp) => {
  return dispatch => {
    dispatch(authStart());
    const authData = {
      email: email,
      password: pass,
      returnSecureToken: true
    };
    let url = `https://identitytoolkit.googleapis.com/v1/accounts:${
      isSignUp ? "signUp" : "signInWithPassword"
    }?key=AIzaSyCWrhrdrYUpRjvMIumzIMd2HX2HUP0-pOg`;
    axios
      .post(url, authData)
      .then(response => {
        let expirationDate =
          new Date().valueOf() + response.data.expiresIn * 1000;
        localStorage.setItem("token", response.data.idToken);
        localStorage.setItem("expirationDate", expirationDate);
        localStorage.setItem("userId", response.data.localId);
        dispatch(authSuccess(response.data.idToken, response.data.localId));
        dispatch(asyncCheckAuthTimeout(response.data.expiresIn));
      })
      .catch(err => {
        dispatch(authFail(err.response.data.error));
      });
  };
};

export const authStateCheck = () => {
  return dispatch => {
    let token = localStorage.getItem("token");
    if (!token) {
      dispatch(logOut());
    } else {
      let expirationDate = new Date(+localStorage.getItem("expirationDate"));
      if (expirationDate > new Date()) {
        let userId = localStorage.getItem("userId");
        dispatch(authSuccess(token, userId));
        dispatch(
          asyncCheckAuthTimeout(
            (expirationDate.getTime() - new Date().getTime()) / 1000
          )
        );
      } else {
        dispatch(logOut);
      }
    }
  };
};
