import React from 'react';
import Backdrop from'../Backdrop/Backdrop';
import { OPEN_MODIFIER, CLOSED_MODIFIER } from '../../../../constants';
import './Modal.scss';

const Modal = (props) => {
  const {
    show,
    onClickHandler,
    children,
  } = props;

  return (
    <>
      <Backdrop show={show} onClickHandler={onClickHandler} />
      <div className={`Modal ${show ? OPEN_MODIFIER : CLOSED_MODIFIER}`}>
        {children}
      </div>
    </>
  );
};

export default React.memo(Modal);
