import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actionCreators from '../../store/actions/index';
import { getCommonActions } from '../../store/reducers/commonReducer';
import Spinner from '../../components/UI/Spinner/Spinner';
import {
    pathSheetIdPairs,
    getTaskPathByPageId,
    getSpreadSheetUrlWithRanges,
    WRONG_MODIFIER,
    CORRECT_MODIFIER,
} from '../../../constants';
import './SelectCorrectOption.scss';

const actions = getCommonActions('SELECT_CORRECT_OPTION');

const SelectCorrectOption = (props) => {
    const {
        data,
        loading,
        error,
        fetchDataConnect,
    } = props;
    const [selectedValues, setSelectedValues] = useState({});
    const { token } = useParams();
console.log(selectedValues);
    useEffect(() => {
        const {
            fetchStart: fetchStartHandler,
            fetchSuccess: fetchSuccessHandler,
            fetchFail: fetchFailHandler,
        } = actions;

        if (!data || !data.length) {
            const pageId = pathSheetIdPairs['/select-correct-option'];
            const taskPath = getTaskPathByPageId(pageId);
            const requestUrl = getSpreadSheetUrlWithRanges(taskPath, token);
            
            fetchDataConnect(
                requestUrl,
                {
                    fetchStartHandler,
                    fetchSuccessHandler,
                    fetchFailHandler,
                },
            );
        }
    
        // return () => restoreInitialStateConnect();
    }, []);

    const onSelectChangeHandler = (e) => {
        const { value } = e.target;
        const sentenceIndex = Number(e.target.name?.split('_')[1]);

        setSelectedValues({
            ...selectedValues,
            [e.target.name]: value,
        });

        if (value !== data[sentenceIndex].correctOption) {
            e.target.classList.remove(CORRECT_MODIFIER);
            e.target.classList.add(WRONG_MODIFIER);
        } else {
            e.target.classList.remove(WRONG_MODIFIER);
            e.target.classList.add(CORRECT_MODIFIER);
        }
    };

    if (!data) {
        return <Spinner />
    }

    return (
        <div className="SelectCorrectOption wrap">
            <form action="">
                <ul>
                    {data.map((el, ind) => {
                        const [part1, part2] = el.sentence.split('...');
                        const options = el.options.split('+');
                        const selectName = `sentence_${ind}`;

                        return (
                            <li key={el.sentence}>
                                <span>{part1}</span>
                                <select
                                    name={selectName}
                                    id=""
                                    onChange={onSelectChangeHandler}
                                    value={selectedValues[selectName]}
                                >
                                    {
                                        options.map((option, ind) => (
                                            <option value={option} key={option + ind}>{option}</option>
                                        ))
                                    }
                                </select>
                                <span>{part2}</span>
                            </li>
                        );
                    })}
                </ul>
            </form>
        </div>
    );
};

const mapStateToProps = (state) => ({
    data: state.selectCorrectOption.fetchedData,
    loading: state.selectCorrectOption.loading,
    error: state.selectCorrectOption.error,
});

const mapDispatchToProps = (dispatch) => ({
    fetchDataConnect: (url, options) => dispatch(actionCreators.asyncFetchStart(url, options)),
    restoreInitialStateConnect: () => dispatch(actions.restoreInitialState()),
});

export default connect(mapStateToProps, mapDispatchToProps)(SelectCorrectOption);