import PropTypes from 'prop-types';
import "./Button.scss";

/**
 * Represents Button Component.
 *
 * @param {object} props - Props.
 * @param {string} [classNames=''] - Additional class name modifiers.
 * @param {string} [type='button'] - Type of button.
 * @param {boolean} [disabled] - If it is disabled.
 * @param {Function} [onClickHandler=() => {}] - On click handler.
 * @param {string|JSX.Element} children - Button text or inner elements.
 * @returns {JSX.Element} - Button.
 */
const Button = (props) => {
  const {
    classNames = '',
    type = 'button',
    disabled,
    onClickHandler = () => {},
    children,
  } = props;

  return (
    <button
      className={`Button ${classNames}`}
      onClick={onClickHandler}
      disabled={disabled}
      type={type}
    >
      {children}
    </button>
  );
};

Button.propTypes = {
  classNames: PropTypes.string,
  type: PropTypes.string,
  disabled: PropTypes.bool,
  onClickHandler: PropTypes.func,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.string,
  ]).isRequired,  
};

export default Button;
