import {
    updateState,
    processJSONfromGoogleSpreadsheet as processJSON,
    processJSONfromGoogleSpreadsheetWithRanges as processJSONWithRanges,
  } from "../../utilities/utilities";

const getInitialStateInstance = () => ({
  fetchedData: null,
  loading: false,
  error: null,
});

const fetchSuccessAux = (state, action) => {
  const {
    data = action,
  } = action;
    return updateState(state, {
      loading: false,
      fetchedData: data.valueRanges
        ? processJSONWithRanges(data)
        : processJSON(data),
    });
  };
  
  const fetchFailAux = (state, action) => {
    return updateState(state, { loading: false, error: action.error });
  };
  
  const fetchStartAux = (state, action) => {
    return updateState(state, { loading: true });
  };

  const commonActions = {
    fetchStart: (state, payload) => (
      fetchStartAux(state, payload)
    ),
    fetchSuccess: (state, payload) => (
      fetchSuccessAux(state, payload)
    ),
    fetchFail: (state, payload) => (
      fetchFailAux(state, payload)
    ),
    restoreInitialState: () => getInitialStateInstance(),
  };

  export {
    fetchStartAux,
    fetchFailAux,
    fetchSuccessAux,
    getInitialStateInstance,
    commonActions,
  };