import { useEffect } from "react";
import {connect} from 'react-redux';
import { useNavigate, useParams } from "react-router-dom";
import * as actionCreators from '../../store/actions/index';
import { getCommonActions } from '../../store/reducers/commonReducer';
import Spinner from '../../components/UI/Spinner/Spinner';
import Button from '../../components/UI/Button/Button';
import {
	getSpreadSheetUrlWithRanges,
	getTaskPathByPageId,
	pathSheetIdPairs,
} from '../../../constants';
import './Text.scss';

const actions = getCommonActions('TEXTS');

const Text = (props) => {
    const {
		fetchDataConnect,
		data,
		loading,
	} = props;
    const navigate = useNavigate();
    const { token } = useParams();

    useEffect(() => {
		const requestUrl = getTaskPathByPageId(2);
        const {
			fetchStart: fetchStartHandler,
			fetchSuccess: fetchSuccessHandler,
			fetchFail: fetchFailHandler,
		} = actions;

		fetchDataConnect(
			getSpreadSheetUrlWithRanges(requestUrl, token),
            {
				fetchStartHandler,
				fetchSuccessHandler,
				fetchFailHandler,
			},
		);
	}, []);

    if (loading) {
		return  <Spinner />;
	}

    if(!data) {
        return null;
    }

    const {
        title,
        subtitle,
        text,
        linksToTasks,
    } = data[0];

    return (
        <section className="rites-of-passage">
            <article>
                <h1>{title}</h1>
                <p className="subhead">{subtitle}</p>
                <div dangerouslySetInnerHTML={{ __html: text }} />
            </article>
            <div className="task">
                {linksToTasks.split(' ').map((el, ind) => (
                    <Button
                        key={el}
                        classNames="btn-3d -green-pale -short"
                        onClickHandler={() => navigate(el)}
                    >
                        {`Go to task ${ind + 1}`}
                    </Button>
                ))}
			</div>
        </section>
    );
};

const mapStateToProps = (state) => ({
	data: state.texts.fetchedData,
    loading: state.texts.loading,
    error: state.texts.error,
});

const mapDispatchToProps = (dispatch) => ({
    fetchDataConnect: (url, options) => dispatch(actionCreators.asyncFetchStart(url, options)),
    restoreInitialStateConnect: () => dispatch(actions.restoreInitialState()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Text);