import './Backdrop.css';

/**
 * Semi-transparent black overlay used in modals and popups.
 *
 * @param {object} props - Props.
 * @param {Function} props.onClickHandler - On overlay click handler.
 * @param {boolean} props.show - Should overlay be shown.
 * @returns {JSX.Element|null} Overlay.
 */
const Backdrop = (props) => {
  return props.show ? <div className="Backdrop" onClick={props.onClickHandler} /> : null;
};

export default Backdrop;
