import { useState } from 'react';
import "./Input.css";

const Input = (props) => {
  const [value, setValue] = useState('');
  let inputElement = null;
  let errorMessage = null;
  let isValid = "";
  if (props.invalid && props.shouldValidate && props.touched) {
    isValid = " Invalid";
    errorMessage = (
      <p className="validation-error">Please, enter the correct data</p>
    );
  }

  switch (props.elementType) {
    case "input":
      inputElement = (
        <input
          className={"Input" + isValid}
          {...props.data}
          onChange={props.change}
        />
      );
      break;
    case "textarea":
      inputElement = <textarea {...props.data} onChange={props.change} />;
      break;
    case "select":
      inputElement = (
        <select
          className={"Select" + isValid}
          value={props.value}
          onChange={props.change}
        >
          {props.data.options.map(elem => (
            <option value={elem.value} key={elem.value}>
              {elem.text}
            </option>
          ))}
        </select>
      );
      break;
    default:
      inputElement = (
        <input
          className={"Input" + isValid}
          {...props.data}
          onChange={props.change || ((e) => setValue(e.target.value))}
          value={props.value || value}
        />
      );
  }

  return (
    <div className="InputContainer" data-placeholder={props.placeholder}>
      <label className="Label" htmlFor={props.label}>
        {props.label}
      </label>
      {inputElement}
      {errorMessage}
    </div>
  );
};

export default Input;
