import * as actionTypes from "./actionTypes";
import axios from "axios";

export const fetchStart = () => ({
  type: actionTypes.FETCH_START
});

export const fetchSuccess = (data, options = {}) => {
  const { bare } = options;

  return {
    type: actionTypes.FETCH_SUCCESS,
    data,
    bare,
  };
};

export const fetchFail = (error) => ({
  type: actionTypes.FETCH_FAIL,
  error,
});

export const asyncFetchStart = (url, options = {}) => {
  const {
    bare,
    fetchStartHandler = fetchStart,
    fetchSuccessHandler = fetchSuccess,
    fetchFailHandler = fetchFail,
  } = options;

  return (dispatch) => {
    dispatch(fetchStartHandler());

    axios
      .get(url)
      .then(res => dispatch(fetchSuccessHandler(res.data, { bare })))
      .catch(err => dispatch(fetchFailHandler(err.response)));
  };
};

export const clearData = (category) => ({
  type: actionTypes.CLEAR_DATA,
  category: category
});

export const restoreInitialState = () => ({
  type: actionTypes.RESTORE_INITIAL_STATE,
});