import { useState, useEffect } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";

import * as actionCreators from "../../store/actions/index";
import Toolbar from "../../components/Navigation/Toolbar/Toolbar";
import SideDrawer from "../../components/Navigation/SideDrawer/SideDrawer";
import Overlay from "../../components/UI/Overlay/Overlay";
import Footer from "../../components/Footer/Footer";

const Layout = (props) => {
  const [sideDrawerIsOpen, setSideDrawerIsOpen] = useState(false);
  const { onAuth, children } = props;
  const { isAuth } = useParams();

  useEffect(() => {
    onAuth();

    return () => setSideDrawerIsOpen(false);
  }, []);

  const toggleSideDrawerHandler = (e) => {
    setSideDrawerIsOpen((prevState) => !prevState);
  };

  return (
    <>
      <header>
        {/* <SideDrawer
          isAuth={props.isAuth}
          isOpen={sideDrawerIsOpen}
          clickHandler={toggleSideDrawerHandler}
        /> */}
        <Toolbar isAuth={isAuth} />
      </header>
      <main>{children}</main>
      <Footer />
    </>
  );
}

const mapStateToProps = state => {
  return {
    isAuth: state.auth.token
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onAuth: () => dispatch(actionCreators.authStateCheck())
  };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Layout);
