import { Link } from "react-router-dom";
import "./Logo.scss";
import Img from '../../../../static_assets/images/us-flag.svg';

const Logo = () => {
  return (
    <Link className="Logo" to="/">
      <img src={Img} alt="logo" />
      <p>Learn English<span className="subdescription"> - Make Progress</span></p>
    </Link>
  );
};

export default Logo;
