import { Remarkable } from 'remarkable';

/**
 * Shakes array data in random order.
 *
 * @param {Array} arr - Array.
 * @returns {Array} Shaken array.
 */
export const shakeArray = (arr) => {
  return arr.sort(() => Math.random() - 0.5);
};

export const updateState = (oldState, updates) => {
  return {
    ...oldState,
    ...updates
  };
};

if ('scrollRestoration' in window.history) {
  window.history.scrollRestoration = 'manual';
}

export const md = new Remarkable('full', {
  html: true,
  typographer: true,
});
md.core.ruler.enable(['abbr']);
md.inline.ruler.enable(['ins', 'mark']);

export const processSentencesJSONfromGoogleSpreadsheet = (data) => {
  const arr = [];

  data.values.forEach((el, ind) => {
    if (ind) {
      arr.push({
        correctSentence: el[0],
        translation: el[1],
        wordSets: el[2].split('|').map((el) => el.split('+')),
        taskName: el[3],
      });
    }
  });

  return arr;
};

const toCamelCase = (phrase) => (
  phrase.split(' ').map((word, ind) => (
    ind 
      ? word[0].toUpperCase() + word.slice(1)
      : word[0].toLowerCase() + word.slice(1)
  )).join('')
);

export const processJSONfromGoogleSpreadsheetWithRanges = (data) => {
  const titles = data.valueRanges[0].values[0].map((title) => toCamelCase(title));
  let arr = [];

  data.valueRanges[1].values.forEach((elem, index) => {
      const obj = {};
      titles.forEach((title, ind) => obj[title] = elem[ind]);
      arr.push(obj);
  });

  return arr;
};

export const processJSONfromGoogleSpreadsheet = (data) => {
  const titles = data.values[0].map((title) => toCamelCase(title));
  let arr = [];

  data.values.forEach((elem, index) => {
    if (index) {
      const obj = {};
      titles.forEach((title, ind) => obj[title] = elem[ind]);
      arr.push(obj);
    }
  });

  return arr;
};

export const isMobile = () => {
  let devicesList = navigator.userAgent || navigator.vendor || window.opera;
  let expr = /(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i;
  return expr.test(devicesList);
};

export const voiceOver = (text, options = {}) => {
  const {
    lang = 'en',
    rate = .8,
  } = options;

  if (!("speechSynthesis" in window)) return;
  const speech = new SpeechSynthesisUtterance();
  const speak = window.speechSynthesis;
  speech.lang = lang;
  speech.rate = rate;
  speech.text = text;
  speak.speak(speech);
};

export const checkTextOverflow = (selector) => {
    return selector.offsetWidth < selector.scrollWidth
    || selector.offsetHeight < selector.scrollHeight;
};

export const adjustTextContent = (selector) => {
    const minFontSize = .6;
    const curentFontSize = parseInt(window.getComputedStyle(selector).fontSize);
    
    selector.classList.add('-with-scroll-horizontal');
    selector.style.fontSize = `${curentFontSize - .1}rem`;
    while (curentFontSize >= minFontSize && checkTextOverflow(selector)) adjustTextContent(selector);
};