import './ProgressBar.scss';


const minimalProgressPercent = 5;

const ProgressBar = (props) => {
  const {
    total,
    done,
    correct,
    additionalClassNames = '',
  } = props;
  const donePercentage = Math.ceil((done / total) * 100) || minimalProgressPercent;
  const correctPercentage = ~~((correct / total) * 100);

  return (
    <div className="ProgressBar">
      <div className={`progress-bar ${additionalClassNames}`}>
        <div className="progress-value" style={{ width: `${donePercentage}%` }}></div>
      </div>
      <span className="progress-percentage">
        {correctPercentage}% correct
      </span>
      <span className="progress-correct">
        {correct} out of {total} is correct
      </span>
    </div>
  );
};

export default ProgressBar;
