import React, {Component} from 'react';
import {connect} from 'react-redux';

import Spinner from '../../components/UI/Spinner/Spinner';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import withRouter from '../../HOC/withRouter';
import { getCommonActions } from '../../store/reducers/commonReducer';
import * as actionCreators from '../../store/actions/index';
import {
    getSpreadSheetUrlWithRanges,
    getTaskPathByPageId,
    CORRECT_MODIFIER,
    WRONG_MODIFIER,
} from '../../../constants'; 

import './FillGaps.scss';

const actions = getCommonActions('fillGaps');

class FillGaps extends Component {
    state = {
        data: null,
        showPossibleOptions: false,
        form: [
            {
                elementType: "input",
                elementConfig: {
                  type: "text",
                  maxLength: 20,
                  autoComplete: 'off'
                },
                value: "",
                touched: false,
                correct: false
            }
        ],
        controls: [
            {
                name: 'ShowScore'
            },
            {
                name: 'ShowAnswers'
            },
            {
                name: 'ShowVariants',
            },
            {
                name: 'Restart',
                type: 'reset'
            },
        ]
    };

    refSentense = React.createRef();

    componentDidMount() {
        const { token } = this.props.params;
        const requestUrl = getSpreadSheetUrlWithRanges(getTaskPathByPageId(11), token);
        const {
			fetchStart: fetchStartHandler,
			fetchSuccess: fetchSuccessHandler,
			fetchFail: fetchFailHandler,
		} = actions;

        this.props.fetchDataConnect(
            requestUrl,
            {
				fetchStartHandler,
				fetchSuccessHandler,
				fetchFailHandler,
			},
        );
    }

    componentDidUpdate(prevProps, prevState) {
		if(prevProps && prevProps !== this.props) {
            if(!this.props.data) return;
            const updatedForm = [];
            this.props.data.forEach(el => {
                if (el.correctWord) {
                    const inputElem = {...this.state.form[0]};
                    const config = {...inputElem.elementConfig, name: el.correctWord.toLowerCase()};
                    inputElem.elementConfig = config;
                    updatedForm.push(inputElem);
                }
            });
			this.setState({data: this.props.data, form: updatedForm});
		}
    }
    
    onSubmitHandler = e => {
        e.preventDefault();
    };

    onInputHandler = (e, id) => {
        const updatedForm = [...this.state.form];
        updatedForm[id].value = e.target.value.toLowerCase();
        if(e.target.value.toLowerCase() === e.target.name) {
            updatedForm[id].correct = true; 
        }else {
            updatedForm[id].correct = false; 
        }
        this.setState({form: updatedForm});
    };

    showScoreHandler = () => {
        let totalCorrect = this.state.form.filter(el => el.correct).length;
        alert(`Your score is ${totalCorrect}/${this.state.form.length}`);
        this.showAnswersHandler();
    };

    showAnswersHandler = () => {
        Array.from(this.refSentense.current.children).forEach((el,index) => {
            if(this.state.form[index].correct) {
                el.classList.add(CORRECT_MODIFIER);
            }else {
                el.classList.add(WRONG_MODIFIER);
            }
        });
    };

    showCorrectAnswersHandler = () => {
        const updatedForm = [...this.state.form];
        updatedForm.forEach(el => el.value = el.elementConfig.name);
        this.setState({form: updatedForm});
    };

    showPossibleOptionsHandler = () => {
        this.setState((prevState) => ({ showPossibleOptions: !prevState.showPossibleOptions }));
    };

    onResetHandler = () => {
        let updatedForm = [...this.state.form];
        updatedForm.forEach(el => {
            el.value = '';
            if(el.correct) {
                el.correct = false;
            }
        });
        Array.from(this.refSentense.current.children).forEach((el) => {
            el.classList.remove(CORRECT_MODIFIER, WRONG_MODIFIER)
        });
        this.setState({form: updatedForm});
    };

    render() {
        let data = null;
        if(this.props.loading) {
            data = <Spinner />;
        }
        if(this.state.data) {
            data = (
                <div className='FillGaps'>
                    <form onSubmit={this.onSubmitHandler}>
                        <ol ref={this.refSentense}>
                            {this.state.data.map((el, index) => (
                                el.sentence
                                ? (
                                    <li key={el.word + index}>
                                        {el.sentence.split('|+|')[0]}
                                        <Input data={this.state.form[index].elementConfig} value={this.state.form[index].value} change={(e) => this.onInputHandler(e, index)} />
                                        {el.sentence.split('|+|')[1]}
                                    </li>
                                )
                                : null
                            ))}
                        </ol>
                        <div className='FillGapsControls'>
                            {
                                this.state.controls.map(el => (
                                    <Button classNames='-plain' 
                                        {...el} 
                                        key={el.name}
                                        onClickHandler={
                                            el.name === 'ShowScore'
                                            ? this.showScoreHandler :
                                            el.name === 'ShowAnswers'
                                            ? this.showCorrectAnswersHandler :
                                            el.name === 'ShowVariants'
                                            ? this.showPossibleOptionsHandler
                                            : this.onResetHandler
                                        }
                                    >{el.name}</Button>
                                ))
                            }
                        </div>
                        <div className='Total'>
                            {this.state.showPossibleOptions
                                ? (
                                    <ul>
                                        {this.props.data.map((el, index) => (
                                            <li key={el + index}>{el.word}</li>
                                        ))}
                                    </ul>
                                ) : null
                            }
                        </div>
                    </form>
                </div>
            );
        }
        return data;
    }
}

const mapStateToProps = (state) => ({
    data: state.fillGaps.fetchedData,
    loading: state.fillGaps.loading,
    error: state.fillGaps.error,
});

const mapDispatchToProps = (dispatch) => ({
    fetchDataConnect: (url, options) => dispatch(actionCreators.asyncFetchStart(url, options)),
    restoreInitialStateConnect: () => dispatch(actions.restoreInitialState()),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(FillGaps));