import { NavLink, Link } from 'react-router-dom';
import navMenuData from '../Navigation/NavigationItems/navMenuData';
import './Footer.scss';

const Footer = (props) => {
    return (
        <footer className="Footer">
            <div className="footer-content wrap">
                <div className="row-top">
                    <div className="about">
                        <h6>About</h6>
                        <p className="text-justify">makeprogressinenglish.com<br /><i>NOTICE! </i> We say 'Marry Christmas, One Nation Under God, &amp; Give Thanks to Our Troops'. If this offends you, leave.</p>
                    </div>
                    <div className="categories">
                        <h6>Categories</h6>
                        <ul className="footer-links">
                            {navMenuData.map((el) => (
                                <li key={el.path}>
                                    <NavLink end to={el.path}>
                                        {el.text}
                                    </NavLink>
                                </li>
                            ))}     
                        </ul>
                    </div>
                    <div className="quick-links">
                        <h6>Quick Links</h6>
                        <ul className="footer-links">
                            {navMenuData.find((el) => el.children).children.map((el) => (
                                <li key={el.path}>
                                    <NavLink end to={el.path}>
                                        {el.text}
                                    </NavLink>
                                </li>
                            ))} 
                        </ul>
                    </div>
                </div>
                <hr />
                <div className="row-bottom">
                    <p className="copyright-text">Copyright &copy; {new Date().getFullYear()} All Rights Reserved by&nbsp;
                        <Link to="/#">Make Progress</Link>.
                    </p>
                    <div>
                        <ul className="social-icons">
                            <li>
                                <Link className="-facebook" to="/#"></Link>
                            </li>
                            <li>
                                <Link className="-instagram" to="/#"></Link>
                            </li>
                            <li>
                                <Link className="-youtube" to="/#"></Link>
                            </li>
                            <li>
                                <Link className="-linkedin" to="/#"></Link>
                            </li>   
                        </ul>
                    </div>
                </div>
            </div>
        </footer>
    );
};

export default Footer;