import * as actionTypes from "../actions/actionTypes";
import { updateState } from "../../utilities/utilities";

const initialState = {
  token: null,
  userId: null,
  loading: false,
  error: null
};

const authStartAux = (state, action) => {
  return updateState(state, { loading: true, error: null });
};

const authSuccessAuth = (state, action) => {
  const updates = {
    token: action.idToken,
    userId: action.userId,
    error: null,
    loading: false
  };
  return updateState(state, updates);
};

const authFailAux = (state, action) => {
  return updateState(state, { error: action.error, loading: false });
};

const authLogOutAux = (state, action) => {
  return updateState(state, { token: null, userId: null });
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.AUTH_START:
      return authStartAux(state, action);
    case actionTypes.AUTH_SUCCESS:
      return authSuccessAuth(state, action);
    case actionTypes.AUTH_FAIL:
      return authFailAux(state, action);
    case actionTypes.AUTH_LOG_OUT:
      return authLogOutAux(state, action);
    default:
      return state;
  }
  return state;
};

export default authReducer;
