import { NavLink } from "react-router-dom";
import { ACTIVE_MODIFIER } from "../../../../constants";
import navMenuData from "./navMenuData";
import useClickOutside from '../../../hooks/useClickOutside';
import "./NavigationItems.scss";

const openSubmenu = (e) => {
  e.currentTarget.classList.toggle(ACTIVE_MODIFIER);
};

const closeSubmenu = (e, ref) => {
  if (ref.current) {
    ref.current.querySelector('.has-submenu').classList.remove(ACTIVE_MODIFIER);
  }
}

const ProtectedRoutes = () => (
  <>
    <li className="NavigationItem">
      <NavLink end to="/statistics">
        My Statistics
      </NavLink>
    </li>
    <li className="NavigationItem">
      <NavLink end to="/logout">
        Logged out
      </NavLink>
    </li>
  </>
);

const renderNavItems = (navData) => {
  return (
    navData.map((navItem) => {
      const {
        children,
        color,
        text,
        path,
      } = navItem;

      return children
        ? (
          <li
            className="NavigationItem has-submenu"
            onClick={openSubmenu}
            key={text}
          >
            <a data-color={color}>{text}</a>
            <ul className='submenu'>
              {renderNavItems(children)}
            </ul>
          </li>
        )
        : (
          <li className="NavigationItem" key={text}>
            <NavLink end to={path} data-color={color}>
              {text}
            </NavLink>
          </li>
        );
    })
  );
};

const NavigationItems = (props) => {
  const { isAuth } = props;
  const clickOutsideRef = useClickOutside(closeSubmenu, () => true);
  
  return (
    <ul className='NavigationItems' ref={clickOutsideRef}>
      {renderNavItems(navMenuData)}
      {isAuth
        ? (
          <ProtectedRoutes />
        )
        : null}
    </ul>
  );
};

export default NavigationItems;
