import { ActionCreatorsFactory, ReducerFactory } from '../reducers';
import {
  getInitialStateInstance,
  commonActions,
} from './commonReducerUtils';

const MODULE_NAME = 'SpellWord';

const initialState = getInitialStateInstance();

export const actions = ActionCreatorsFactory(
  { moduleName: MODULE_NAME },
  {
   ...commonActions,
   // Component-specific reducers can go here
  }
);

const spellWordReducer = ReducerFactory(initialState, actions);

export default spellWordReducer;