import { NavLink } from "react-router-dom";

import './List.scss';

/**
 * List Component.
 *
 * @param {object} props - Props.
 * @param {Array} props.content - Content.
 * @param {boolean} props.divider - If to show a bottom border for list-item.
 * @returns {JSX.Element} - List component. 
 */
const List = (props) => {
    const {
        content,
        divider,
    } = props;

    return (
        <ul className="List">
            {content.map((el) => {
                const {
                    url,
                    desc,
                    subtitle,
                } = el;
                const listClassNames = `list-item${divider ? ' -with-divider' : ''}`;
                const listContent = (
                    <>
                        {desc}
                        {subtitle ? (
                            <div className="sub-title">
                                {subtitle}
                            </div>
                        ) : null}
                    </>
                );

                return url ? (
                    <li key={url} className={listClassNames}>
                        <NavLink end to={url}>
                            {listContent}
                        </NavLink>
                    </li>
                ) : (
                    <li key={url} className={listClassNames}>
                        {listContent}
                    </li>
                );
            })}
        </ul>
    );
};

export default List;