import React, { Component } from "react";
import { connect } from "react-redux";
import { Navigate } from "react-router-dom";

import * as actionCreators from "../../store/actions/index";
import "./Auth.css";
import checkValidity from "../../utilities/formsValidation";
import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";
import Spinner from "../../components/UI/Spinner/Spinner";

class Auth extends Component {
  state = {
    controls: {
      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "johndoe@gmail.com"
        },
        value: "",
        validation: {
          required: true,
          isEmail: true
        },
        valid: false,
        touched: false
      },
      password: {
        elementType: "input",
        elementConfig: {
          type: "password",
          placeholder: "johndoe11234rdds"
        },
        value: "",
        validation: {
          required: true,
          minLength: 6
        },
        valid: false,
        touched: false
      }
    },
    isSignUp: true
  };

  inputChangedHandler = (e, type) => {
    const updatedControls = {
      ...this.state.controls,
      [type]: {
        ...this.state.controls[type],
        value: e.target.value,
        valid: checkValidity(
          e.target.value,
          this.state.controls[type].validation
        ),
        touched: true
      }
    };
    this.setState({ controls: updatedControls });
  };

  changeAuthMethod = () => {
    this.setState(prevState => {
      return { isSignUp: !prevState.isSignUp };
    });
  };

  submitHandler = e => {
    e.preventDefault();
    this.props.onAuth(
      this.state.controls.email.value,
      this.state.controls.password.value,
      this.state.isSignUp
    );
  };

  render() {
    let formContent = [];
    for (let key in this.state.controls) {
      formContent.push({
        data: { ...this.state.controls[key] },
        id: key
      });
    }
    let form = formContent.map(elem => (
      <Input
        elementType={elem.data.elementType}
        key={elem.id}
        data={elem.data.elementConfig}
        value={elem.data.value}
        invalid={!elem.data.valid}
        shouldValidate={!!elem.data.validation}
        touched={elem.data.touched}
        change={e => this.inputChangedHandler(e, elem.id)}
      />
    ));
    if (this.props.loading) {
      form = <Spinner />;
    }
    let errorMessage = null;
    let redirect = null;
    if (this.props.error) {
      errorMessage = <p>{this.props.error.message}</p>;
    }
    if (this.props.isAuth) {
      if (this.props.buildStart) {
        redirect = <Navigate to="/checkout" />;
      } else {
        redirect = <Navigate to="/" />;
      }
    }

    return (
      <div className="Auth">
        {redirect}
        {errorMessage}
        <form onSubmit={this.submitHandler}>
          {form}
          <Button classNames="Success">Submit</Button>
        </form>
        <Button classNames="Danger" onClickHandler={this.changeAuthMethod}>
          Switch to {this.state.isSignUp ? "Sign In" : "Sign Up"}
        </Button>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    loading: state.auth.loading,
    error: state.auth.error,
    isAuth: state.auth.token,
    buildStart: state.burgerBuilder.buildStart
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onAuth: (email, pass, isSignUp) =>
      dispatch(actionCreators.asyncAuth(email, pass, isSignUp))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Auth);
