import React, { useEffect } from 'react';
import Button from "../UI/Button/Button";

const cardControls = React.forwardRef((props, ref) => {
  const {
    taskFinished,
  } = props;

  return (
    <div className="card-controls">
      <div className="additions">
        <div className="image-container -bordered">
          <img
            src={
              props.data.img
                ? `${props.data.img}`
                : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSols5HZxlQWyS9JY5d3_L9imbk0LiziHiyDtMZLHt_UNzoYUXs2g"
            }
            alt={props.data.word}
            ref={ref}
          />
        </div>
        <div className="word-context">
          <h5 className="context-title">Context of usage:</h5>
          <p className="context">
            {props.data.usage
              ? props.data.usage
              : "There is no context for this word"}
          </p>
        </div>
      </div>
      <form
          className="controls"
          onClick={props.click}
          onSubmit={e => e.preventDefault()}
        >
           <Button
            classNames="voice-over -green-pale -with-gradient"
            onClickHandler={props.voiceOver}
          >
            <i>Spell it</i>
          </Button>
          <Button
            classNames="skip-word -red-pale -with-gradient"
          >
              I do not know
          </Button>
          {taskFinished
            ? (
              <Button
                classNames="repeat-task -purple-pale -with-gradient"
              >
                  Repeat Task
              </Button>
            )
            : (
              <Button
                classNames="next-word -blue-pale -with-gradient"
              >
                  Next word
              </Button>
            )}
          {taskFinished ? (
            <Button
              classNames="show-recap -yellow-pale -with-gradient"
            >
                Recap
            </Button>
          ) : null}
        </form>
    </div>
  );
});

export default cardControls;