import React, { Component, Fragment, useRef, useState, useEffect } from "react";
import { connect } from "react-redux";
import { useParams } from 'react-router-dom';

import * as actionCreators from '../../store/actions/index';
import { getCommonActions } from '../../store/reducers/commonReducer';
import lazyLoadImages from "../../utilities/imagesLazyLoad";
import { isMobile, shakeArray, voiceOver } from '../../utilities/utilities';
import Button from "../../components/UI/Button/Button";
import Modal from "../../components/UI/Modal/Modal";
import Spinner from "../../components/UI/Spinner/Spinner";
import ProgressBar from "../../components/UI/ProgressBar/ProgressBar";
import CardControls from "../../components/CardControls/CardControls";
import WordCard from './WordCard';
import {
  CORRECT_MODIFIER,
  WRONG_MODIFIER,
  HIDDEN_MODIFIER,
  pathSheetIdPairs,
  getTaskPathByPageId,
  getSpreadSheetUrlWithRanges,
} from '../../../constants';

import { Present } from "../../HOC/Themes/Christmas/Christmas";
import './WordsConstructor.scss';

const actions = getCommonActions('WORDS_CONSTRUCTOR');

const letterInputClassName = 'blank-letter';
const inputConfig = {
  maxLength: 1,
  className: letterInputClassName,
  readOnly: isMobile(), // Allow click on input on mobile, but not to enter data
};
const showPresentWhenPercentCorrect = 70;
const attemptsAllowedQty = 3;

const checkKeyboardLayout = (char) => {
  const engChars = /[a-z-\s]/i;

  return !engChars.test(char);
};

const Words = (props) => {
  const {
    fetchDataConnect,
    restoreInitialStateConnect,
    data,
    loading,
    error,
  } = props;
  const [currentWordIndex, setCurrentWordIndex] = useState(0);
  const [showPresent, setShowPresent] = useState(false);
  const [showRecap, setShowRecap] = useState(false);
  const [correctWordsQty, setCorrectWordsQty] = useState(0);
  const [guessedPartOfWord, setGuessedPartOfWord] = useState('');
  const [modalIsOn, setModalIsOn] = useState(false);
  const [attempts, setAttempts] = useState(attemptsAllowedQty);
  const imgBlurredRef = useRef(null);
  const { token } = useParams();
  const taskFinished = currentWordIndex === data?.length - 1;

  useEffect(() => {
    const pageId = pathSheetIdPairs['/words-constructor'];
    const taskPath = getTaskPathByPageId(pageId);
    const requestUrl = getSpreadSheetUrlWithRanges(taskPath, token || '2:10');
    const {
      fetchStart: fetchStartHandler,
      fetchSuccess: fetchSuccessHandler,
      fetchFail: fetchFailHandler,
  } = actions;

    fetchDataConnect(
        requestUrl,
        {
            fetchStartHandler,
            fetchSuccessHandler,
            fetchFailHandler,
        },
    );

    return () => restoreInitialStateConnect();
  }, []);

  const closeModal = () => {
    setModalIsOn((prev) => !prev);
  };

  const checkPercentage = () => {
    if (
      correctWordsQty + 1 / data.length >= showPresentWhenPercentCorrect / 100
      && currentWordIndex + 1 === data.length
    ) {
      setShowPresent(true);
    }
  };

  const clearLetters = (type) => {
    const blankLetters = document.querySelectorAll(".blank-letter");
    const hiddenLetters = document.querySelectorAll(`.${HIDDEN_MODIFIER}`);
    const roster = document.querySelector(".roster");
    if (roster) {
      roster.children[currentWordIndex].classList.add(type);
    }
    blankLetters.forEach((elem) => elem.classList.remove(CORRECT_MODIFIER, WRONG_MODIFIER));
    hiddenLetters.forEach((elem) => elem.classList.remove(HIDDEN_MODIFIER));
    blankLetters.forEach((elem) => {
      elem.value = '';
    });
  };

  const repeatTask = () => {
    setCurrentWordIndex(0);
    setShowPresent(false);
    setShowRecap(false);
    setCorrectWordsQty(0);
    setGuessedPartOfWord('');
    setModalIsOn(false);
    setAttempts(attemptsAllowedQty);
  };

  const handleControlButtons = (e) => {
    if (e.target.classList.contains("next-word")) {
      if (currentWordIndex === data.length - 1) return;
        if (attempts === attemptsAllowedQty) {
          clearLetters(CORRECT_MODIFIER);
        } else {
          clearLetters(WRONG_MODIFIER);
        }
        setAttempts(attemptsAllowedQty);
        setCurrentWordIndex((prev) => prev += 1);
        setGuessedPartOfWord('');
    if (imgBlurredRef.current)
      imgBlurredRef.current.style.filter = '';   
    } else if (e.target.classList.contains("skip-word")) {
      clearLetters(WRONG_MODIFIER);
      setGuessedPartOfWord(data[currentWordIndex].word);
      voiceOver(data[currentWordIndex].word);
    } else if (e.target.classList.contains('repeat-task')) {
      repeatTask();
    } else if (e.target.classList.contains('show-recap')) {
      setShowRecap((prev) => !prev);
    }
  };

  const hideGuessedLetters = (letter) => {
    const letters = [...document.querySelectorAll(`.letter:not(.${HIDDEN_MODIFIER})`)];
    
    letters.find((el) => el.dataset.value === letter)?.classList.add(HIDDEN_MODIFIER);
  };

  const checkAnswer = (e) => {
    const target = e.target.closest(`.${letterInputClassName}`) || e.target.closest('.letter');
    const letterInput = document.querySelectorAll(`.${letterInputClassName}`)[guessedPartOfWord.length];
    const currentWord = data[currentWordIndex].word;
    const currentLetter = currentWord[guessedPartOfWord.length];
    const currentValue = target.value || target.dataset.value;
    const currentValueLower = currentValue.toLowerCase();

    if (e.type === 'change') {
      const isForbiddenChar = checkKeyboardLayout(currentValueLower);

      if (isForbiddenChar) setModalIsOn(true);
    } else if (e.type === 'click') {

    }

    if (currentValueLower !== currentLetter) {
      setAttempts((prev) => prev -= 1);
      letterInput.classList.add(WRONG_MODIFIER);
    } else {
      hideGuessedLetters(currentLetter);
      letterInput.classList.remove(WRONG_MODIFIER);
      letterInput.classList.add(CORRECT_MODIFIER);

      setGuessedPartOfWord((prev) => prev + currentLetter);

      if (guessedPartOfWord.length + 1 === currentWord.length) {
        imgBlurredRef.current.style.filter = "none";
        document.querySelector('.next-word')?.focus();
        if (attempts === attemptsAllowedQty) {
          setCorrectWordsQty((prev) => prev += 1);
        }
        
        voiceOver(data[currentWordIndex].word);
        checkPercentage();
      }
    }
  };

  if (loading) {
    return <Spinner />;
  } else if (error) {
    return <p>Oops, something went wrong...</p>;
  } else if (data && data.length && data[0].word) {
    const currentWordSet = data[currentWordIndex];

    return (
      <div className="wrapper">
        {showPresent ? <Present /> : null}
        <ProgressBar
            correct={correctWordsQty}
            total={data.length}
            done={correctWordsQty}
            additionalClassNames="-striped"
          />
        <div className="card-container">
          <WordCard
            currentWordSet={currentWordSet}
            guessedPartOfWord={guessedPartOfWord}
            inputConfig={inputConfig}
            checkAnswer={checkAnswer}
          />
          <CardControls
            voiceOver={() => voiceOver(data[currentWordIndex].word)}
            click={handleControlButtons}
            data={data[currentWordIndex]}
            taskFinished={taskFinished}
            ref={imgBlurredRef}
          />
        </div>
        {showRecap && (
          <ol className="roster">{data.map((elem) => (
            <li className="list-item" key={elem.word}>
              <b className="word">{elem.word}</b>
              <span className="translation"> - {elem.translation}</span>
            </li>
          ))}</ol>
        )}
        {modalIsOn ? (
          <Modal show={modalIsOn}>
            <span>Switch your keyboard to English!</span>
            <Button
              classNames="Success"
              onClickHandler={closeModal}
            >
              Ok
            </Button>
          </Modal>
        ) : null}
      </div>
    );
  }
};

const mapStateToProps = (state) => ({
  data: state.wordsConstructor.fetchedData?.map((el) => ({
    ...el,
    shakenArray: el.word && shakeArray(el.word.split('')),
  })),
  loading: state.wordsConstructor.loading,
  error: state.wordsConstructor.error,
});

const mapDispatchToProps = dispatch => {
  return {
    fetchDataConnect: (url, options) => dispatch(actionCreators.asyncFetchStart(url, options)),
    restoreInitialStateConnect: () => dispatch(actions.restoreInitialState()),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Words);
