import React from "react";
import Logo from "../../Logo/Logo";
import NavigationItems from "../NavigationItems/NavigationItems";
import DrawerToggleButton from "./DrawerToggleButton/DrawerToggleButton";
import { OPEN_MODIFIER, CLOSED_MODIFIER } from "../../../../constants";
import "./SideDrawer.scss";

/**
 * Side drawer with navigation menu and quick access links.
 *
 * @param {object} props - Props.
 * @param {boolean} props.isOpen - If it is open.
 * @param {boolean} props.isAuth - If protected routes should be displayed in sidedrawer.
 * @returns {JSX.Element} Side drawer.
 */
const SideDrawer = (props) => {
  const {
    isOpen,
    isAuth,
    clickHandler,
  } = props;

  return (
    <div
      className={`SideDrawer ${isOpen ? OPEN_MODIFIER : CLOSED_MODIFIER}`}
      onClick={clickHandler}
    >
      <DrawerToggleButton />
      <Logo />
      <nav>
        <NavigationItems isAuth={isAuth} />
      </nav>
    </div>
  );
};

export default SideDrawer;
