import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import { getSpreadSheetUrl } from '../../../constants';
import axios from 'axios';
import Button from '../UI/Button/Button';
import FillGaps from '../../containers/FillGaps/FillGaps';
import { processJSONfromGoogleSpreadsheet as processJSON } from '../../utilities/utilities';

import './Video.scss';

const iframeProps = {
    width: '854',
    height: '480',
    frameBorder: '0',
    scrolling: 'no',
    allowFullScreen: true,
};

const Video = (props) => {
    const {
        taskLink,
        children,
        desc,
        url,
    } = props;
    let [state, setState] = useState(null);
    useEffect(() => {
        if(!props.quotes) return;
        axios
        .get(getSpreadSheetUrl('10. Quotes'))
        .then(res => setState(processJSON(res.data)));
    }, []);

    let data = null;
    if(state) {
        data = state.map(el => (
            <div className='QuoteContainer' key={el.opposite}>
                <div className='Quote'>
                    <p>{el.word}</p>
                    <p className='author'>{el.opposite}</p>
                    <hr/>
                </div>
            </div>
        ));
    }
    if(props.mainTask) {
        data = <FillGaps />
    }

    return (
        <div className="Video">
            <div className='video-holder'>
                <div className="frame-holder">
                    <iframe
                        title={desc}
                        src={url}
                        {...iframeProps}
                    />
                </div>
            </div>
            {data}
            {taskLink
                ? (
                    <NavLink end to={taskLink}>
                        <Button classNames="btn-3d -green-pale -short">Hop to Task</Button>
                    </NavLink>
                )
                : null}
            {children}
        </div>
    );
};

export default Video;