import { useState, useEffect } from 'react';
import axios from 'axios';
import {
    FIRST_CAPITAL_LETTER_CHARCODE,
    FIRST_LOWER_LETTER_CHARCODE,
    ENGLISH_LETTERS_QTY,
    ACTIVE_MODIFIER,
    pathSheetIdPairs,
    getTaskPathByPageId,
    getSpreadSheetUrl,
} from '../../../constants'; 
import {
    processJSONfromGoogleSpreadsheet,
    voiceOver,
} from '../../../react_modules/utilities/utilities';
import './Alphabet.scss';

const onLetterClickHandler = (e) => {
    console.log(document.querySelector(`.letter-tile ${ACTIVE_MODIFIER}`));
    document.querySelector(`.letter-tile.${ACTIVE_MODIFIER}`)?.classList.remove(ACTIVE_MODIFIER);
    e.currentTarget.classList.add(ACTIVE_MODIFIER);
};

const renderLettersList = () => {
    return Array.from({ length: ENGLISH_LETTERS_QTY }).map((_, ind) => (
        <li
            className="letter-tile"
            key={FIRST_CAPITAL_LETTER_CHARCODE + ind}
            onClick={onLetterClickHandler}
        >
            {String.fromCharCode(FIRST_CAPITAL_LETTER_CHARCODE + ind)}
            {String.fromCharCode(FIRST_LOWER_LETTER_CHARCODE + ind)}
        </li>
    ));
};

const Alphabet = (props) => {
    const [data, setData] = useState(null);
    const [currentLetter, setCurrentLetter] = useState('a');

    useEffect(() => {
        const pageId = pathSheetIdPairs['/alphabet'];
		const requestUrl = getSpreadSheetUrl(getTaskPathByPageId(pageId));

        axios
            .get(requestUrl)
            .then((res) => {
                const resultProcessed = processJSONfromGoogleSpreadsheet(res.data);

                setData(resultProcessed);
            })
            .catch((err) => {

            });
    }, []);

    return (
        <div className="Alphabet wrap">
            <div className="letters-container">
                <h4>The Alphabet</h4>
                <ul className="letters-wrap">
                    {renderLettersList()}
                </ul>
            </div>
            <div className="words-container">
                <div className="current-letter">
                    {currentLetter.toUpperCase()}
                    {currentLetter}
                </div>
                {data
                    ? (
                        <div
                            className="words-list"
                            onClick={(e) => voiceOver(e.target.closest('span').textContent)}
                            dangerouslySetInnerHTML={{ __html: data.find((el) => el.letter === currentLetter).words }}
                        />
                    )
                : null}
            </div>
        </div>
    );
};

export default Alphabet;