export const getSpreadSheetUrlWithRanges = (sheetListId, sheetRange, spreadsheetId = sheetTableId, dimension = 'ROWS') => (
    `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values:batchGet?ranges=${sheetListId}!1:1&ranges=${sheetListId}!${sheetRange}&majorDimension=${dimension}&key=${API_KEY}`
);

