import {
  LAZY_LOAD_CLASS_NAME,
  LAZY_LOADED_CLASS_NAME,
} from '../../constants';

const lazyLoad = (images, observer) => {
  images.forEach((elem) => {
    if (elem.isIntersecting) {
      elem.target.src = elem.target.getAttribute('data-src');
      elem.target.classList.add(LAZY_LOADED_CLASS_NAME);
      elem.target.classList.remove(LAZY_LOAD_CLASS_NAME);
    }
  });
};

const lazyLoadImages = () => {
  const images = document.querySelectorAll(`.${LAZY_LOAD_CLASS_NAME}`);
// console.log(images);
  if ('IntersectionObserver' in window) {
    const observer = new IntersectionObserver(lazyLoad);
    
    images.forEach((elem) => observer.observe(elem));
  }
};

export default lazyLoadImages;
