// Combined Reducers Dwell Here
import { combineReducers } from 'redux';
import { getCommonActions, getCommonReducer } from './reducers/commonReducer';
import authReducer from "./reducers/auth";

const higherOrderReducer = (reducer, moduleName) => (state, action) => (
	reducer(JSON.parse(JSON.stringify(state)), action)
);

const createRootReducer = (asyncReducers) => combineReducers({
	sentencesConstructor: getCommonReducer(getCommonActions('SENTENCES_CONSTRUCTOR')),
	wordsConstructor: getCommonReducer(getCommonActions('WORDS_CONSTRUCTOR')),
	wordDefenitionMatch: getCommonReducer(getCommonActions('WORDS_DEFENITION_MATCH')),
    blog: getCommonReducer(getCommonActions('BLOG')),
	dragAndDrop: getCommonReducer(getCommonActions('DRAG_AND_DROP')),
	texts: getCommonReducer(getCommonActions('TEXTS')),
	selectCorrectOption: getCommonReducer(getCommonActions('SELECT_CORRECT_OPTION')),
	auth: authReducer,
    ...asyncReducers,
  });

const injectAsyncReducer = (store, name, asyncReducer) => {
    store.asyncReducers[name] = asyncReducer;
    store.replaceReducer(createRootReducer(store.asyncReducers));
};

/**
 * Action Creators Factory.
 *
 * @param {object} settings - Settings.
 * @param {string} settings.moduleName - Module Name.
 * @param {object} actions - Object of action functions, the same as cases from reducer.
 *
 * @returns {object} - Action Creators.
 *
 * @example ActionCreatorsFactory(
 * { moduleName: 'Loading' },
 * { showLoading: (state) => (state.set('isShown', true)) },
 * ) => {
 *	showLoading: () => ({ type: 'Loading/showLoading' }),
 *	['Loading/showLoading']: (state) => state.set('isShown', true),
 * }
 */
 export const ActionCreatorsFactory = (settings, actions) => {
	const { moduleName } = settings;

	const result = {};

	Object.keys(actions).forEach((actionName) => {
		const type = `${moduleName}/${actionName}`;

		/**
		 * Action creator.
		 *
		 * @param {object} payload - Payload.
		 * @returns {{ payload: object, type: string }} - Action.
		 */
		result[actionName] = (payload) => ({ type, payload });
		result[actionName].type = type;

		/**
		 * Action.
		 *
		 * @param {object} state - Current state.
		 * @param {object} payload - Payload.
		 * @returns {object} - Updated state.
		 */
		result[type] = (state, payload) => actions[actionName](state, payload);
	});

	return result;
};

/**
 * Reducer Factory.
 *
 * @param {object} initialState - Initial State.
 * @param {object} actions - Action object.
 * @returns {Function} - Reducer.
 */
export const ReducerFactory = (initialState, actions) => (state = initialState, action = {}) => {
    const { type, payload } = action;
	if (typeof actions[type] === 'function') {
		return actions[type](state, payload) || state;
	}

	return state;
};

export {
	higherOrderReducer,
    createRootReducer,
    injectAsyncReducer,
};